#menu-modal {
  position: absolute;
  top: 5.9375rem;
  border: none;
  width: 100%;
  background-color: transparent;
}

.menu-links {
  margin: 0;
  border-radius: 0.625rem;
  padding-block: 2.5rem;
  padding-inline: 1.5rem;
  list-style-type: none;
  background-color: var(--dk-violet);
}
.menu-links > *:not(:last-child) {
  margin-bottom: 2.5rem;
}
.menu-links li {
  font-size: 0.875rem;
  font-weight: 700;
  text-align: center;
}
.menu-links li hr {
  background-color: var(--gray);
}
.menu-links li a {
  color: var(--white);
  transition: all 200ms ease-in;
}
.menu-links li a:hover:not(.cta) {
  color: var(--cyan);
}

.cta {
  display: block;
  background-color: var(--cyan);
  padding-block: 1rem;
  border-radius: 1.875rem;
  transition: opacity 300ms ease-in;
}
.cta:hover {
  opacity: 0.5;
}
@media (min-width: 43.75em) {
  .cta {
    padding-block: 0.625rem;
    padding-inline: 1.5625rem;
  }
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-weight: 500;
  color: var(--gray);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --cyan: hsl(180, 66%, 49%);
  --dk-violet: hsl(257, 27%, 26%);
  --red: hsl(0, 87%, 67%);
  --gray: hsl(0, 0%, 75%);
  --gray-violet: hsl(257, 7%, 63%);
  --very-dk-blue: hsl(255, 11%, 22%);
  --very-dk-violet: hsl(260, 8%, 14%);
}

:root {
  --font: "Poppins", sans-serif;
}

.container {
  max-width: 69.25rem;
  margin: 0 auto;
  padding-inline: 1.5rem;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 700;
}

p {
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

.nav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 2.5rem;
  padding-bottom: 1.375rem;
}
@media (min-width: 43.75em) {
  .nav {
    gap: 2.8125rem;
  }
}
.nav img {
  width: 7.5rem;
  height: 2.0625rem;
  cursor: pointer;
}
.nav div {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  width: 1.5rem;
  height: 1.3125rem;
  cursor: pointer;
}
@media (min-width: 43.75em) {
  .nav div {
    display: none;
  }
}
.nav div span {
  display: block;
  height: 0.1875rem;
  width: 100%;
  background-color: var(--gray-violet);
}
.nav__links {
  flex-grow: 1;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  gap: 1.875rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 0.875rem;
  font-weight: 700;
}
@media (max-width: 43.7em) {
  .nav__links {
    display: none;
  }
}
.nav__links li a {
  color: var(--gray-violet);
  transition: all 300ms ease-in;
}
.nav__links li a:hover:not(.cta) {
  color: var(--cyan);
}
.nav__links li .cta {
  color: var(--white);
}
.nav__links .separator {
  flex-grow: 1;
}